.agrdesigner {
  background-color: #f1f1f1;
  position: relative;
  padding-left: 80px;
  padding-right: 18px;
  padding-top: 68px;
  padding-bottom: 68px;
  margin-left: auto;
  margin-right: auto;
  height: 100%;
  min-height: 100vh; }
  .agrdesigner:before {
    width: 268px;
    height: 14px;
    position: absolute;
    top: 0;
    left: 48px;
    background-color: #FAC92A;
    content: ''; }

.agrdesigner-nav {
  width: 48px;
  height: 100vh;
  background-color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center; }

.agrdesigner-nav-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  margin-top: auto;
  margin-bottom: 0; }

.agrdesigner-nav-list__item {
  width: 30px;
  height: 30px;
  margin-bottom: 14px; }

.agrdesigner-nav-list__item-anchor {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #202328; }
  .agrdesigner-nav-list__item-anchor:hover {
    color: #FAC92A; }

.agrdesigner-logo {
  width: 70px;
  height: 100px;
  color: #FAC92A;
  margin-left: auto;
  margin-right: auto; }

.agrdesigner-logo-svg {
  width: 100%;
  height: 100%;
  fill: #FAC92A; }

.agrdesigner-anchored-link {
  width: calc(100vw - 48px);
  height: 36px;
  position: fixed;
  bottom: 0;
  left: 48px;
  background-color: #FAC92A;
  color: #202328;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  font-size: 12px;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  opacity: 0.8; }
  @media only screen and (min-width: 640px) {
    .agrdesigner-anchored-link {
      width: 268px;
      right: 0;
      left: auto; } }
  @media only screen and (min-width: 1441px) {
    .agrdesigner-anchored-link {
      left: 50%;
      margin-left: -134px; } }
  .agrdesigner-anchored-link:hover {
    opacity: 1; }

.agrdesigner-title {
  font-size: 26px;
  line-height: 1.5;
  color: #2a2e35;
  letter-spacing: 1px;
  margin-top: 50px;
  margin-bottom: 50px;
  font-family: 'Roboto', arial;
  font-weight: 500; }

.agrdesigner-description {
  font-size: 18px;
  line-height: 2;
  letter-spacing: 1px;
  color: #2a2e35;
  font-family: 'Roboto', arial;
  font-weight: 300; }

.agrdesigner-nav-list__svg {
  fill: currentColor; }

.agrdesigner-nav-list__svg-github {
  width: 19px;
  height: 19px; }

.agrdesigner-nav-list__svg-behance {
  width: 21px;
  height: 18px; }

.agrdesigner-nav-list__svg-linkedin {
  width: 19px;
  height: 19px; }

.agrdesigner-nav-list__svg-twitter {
  width: 19px;
  height: 19px; }

.agrdesigner-nav-list__svg-instagram {
  width: 19px;
  height: 19px; }

.row {
  width: 100%;
  max-width: 770px;
  margin-left: auto;
  margin-right: auto; }
